/* Main Colors */
$color-base : #DBDCE8;
$color-bg : #463848;
$color-text : $color-base;
$color-overlay : $color-base;
$opacity-overlay: .4;
$link-color : $color-base;



/* Fonts */
$font-family : 'Montserrat', sans-serif;
$font-title: $font-family;
$font-size : 20px;
$font-weight : normal;
$letter-spacing : normal;
$line-height : 1.2;
$placeholder: #AAA3B4;



/*container*/
$container-padding-left: 30px;
$container-padding-right: 30px;
$container-width: 1740px + $container-padding-left + $container-padding-right;


/*styles*/

$brs: 10px;