.footer{
  padding-top: 90px;
  padding-bottom: 48px;
}
.footer__top{
  display: flex;
  justify-content: space-between;
  margin-bottom: 90px;
}

.footer__left{
  max-width: 550px;
  width: 100%;
}

.footer-nav{
  display: flex;
  justify-content: space-between;
}

.footer-nav__title{
  font-size: 18px;
  line-height: 120%;
  margin-bottom: 30px; 
  color: #AAA3B4;
}

.footer-nav__link{
  &:not(:last-child){
    margin-bottom: 28px;
  }
  a{
    font-weight: 500;
    font-size: 24px;
    line-height: 120%
  }
}

.footer__right{
  width: 100%;
  max-width: 750px;
}

.footer-right-top {
  display: flex;
  margin-bottom: 48px;
}

.footer-phone{
  font-size: 56px;
  line-height: 90%;
  margin-right: 36px;
}

.footer__time{
  font-weight: 500;
  font-size: 18px;
  line-height: 120%;
  color: #AAA3B4;
}

.footer__contacts{
  display: flex;
  justify-content: space-between;
}

.footer__contacts-item {
  max-width: 350px;
}

.footer__contact{
  font-size: 24px;
  line-height: 140%;
}

.footer__bottom{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__copyright{
  max-width: 830px;
  font-size: 12px;
  line-height: 120%;
  color: #AAA3B4;
}

.footer__socials{
  display: flex;
  justify-content: space-between;
  max-width: 112px;
}

.footer__social{
  border: 1px solid #6E3F52;
  border-radius: 6px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  &:not(:last-child){
    margin-right: 8px;
  }
}
