.section-main{
  padding-top: 208px;
}

.main__top{
  display: flex;
  justify-content: space-between;
  margin-bottom: 50px;
  align-items: flex-end;
}

.main-top-left{
  padding-left: 100px;
}

.main__left{
  padding-bottom: 10px;
}

.main__right{
  justify-content: flex-end;
}


.main__description{
  font-weight: 500;
  font-size: 20px;
  line-height: 130%;
  max-width: 310px;
}

.pink-title{
  font-weight: 800;
  font-size: 56px;
  line-height: 140%;
  color:#E8A9B8;
}

.pink-text{
  font-weight: 600;
  font-size: 14px;
  line-height: 120%;
  color:#E8A9B8;
}

.percent__title{
  margin-bottom: 7px;
}

.percent__info{
  display: flex;
  align-items: center;
}

.percent__icons{
  display: flex;
  margin-right: 19px;
}

.percent__icon{
  &:not(:first-child){
    margin-left: 13px;
  }
}

.main__pic{
  position: relative;
}

.main__panel {
  position: absolute;
  left: 50%;
  bottom: 54px;
  transform: translateX(-50%);
  display: flex;
  justify-content: space-between;
  padding: 18px 24px 18px 30px;
  width: 100%;
  max-width: 980px;
  border-radius: 16px;
  overflow: hidden;
  &:after{
    content: "";
    left: -10%;
    top: -10%;
    width: 120%;
    height: 120%;
    position: absolute;
    background: url(../img/main/pic.png) no-repeat center bottom -54px / 90vw auto;
    filter: blur(15px);
  }
  &:before{
    content: "";
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    background: rgba(170, 163, 180, 0.25);
    z-index: 10;
  }
  & > *{
    position: relative;
    z-index: 10;
  }
}

.main__panel-left{
  padding-right: 20px;
  border-right: 1px solid #AAA3B4;
  display: flex;
}

.main__panel-date{
  font-size: 36px;
  line-height: 90%;
  margin-right: 24px;
}

.main__panel-link{
  font-weight: 500;
  font-size: 16px;
  line-height: 120%;
  max-width: 400px;
  img{
    display: inline-block;
  }
}

.main__panel__btns{
  display: flex;
  justify-content: flex-end;
  .btn{
    font-weight: 600;
    font-size: 13px;
    line-height: 140%;
    &:not(:first-child){
      margin-left: 12px;
    }
  }
}


.section{
  padding-top: 160px;
}

.section-map{
  background: linear-gradient(180deg, #463848 0%, #6E3F52 19.27%, #6E3F52 83.85%, #463848 100%);
  padding-bottom: 500px;
}

#map{
  height: 722px;
  border-radius: 24px;
  overflow: hidden;
  margin-bottom: 24px;
}

.map__switch {
  background: #6E3F52;
  padding: 0px 37px;
  height: 44px;
  border-radius: 11px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 24px;
  top: 24px;
  font-weight: 500;
  font-size: 16px;
  line-height: 100%;
}


.switch{
  width: 36px;
  height: 16px;
  border-radius: 40px;
  background-color: #DBDCE8;
  position: relative;
  transition: .4s;
  cursor: pointer;
  margin-right: 16px;

  &.active{
    background-color: #463848;
    &:after{
      left: 22px;
      background-color: #DBDCE8;
    }
  }
  &:after{
    content: "";
    position: absolute;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: #6E3F52;
    top: 2px;
    left: 2px;
    transition: .4s;
  }
}


.map__tabs{
  position: absolute;
  left: 50%;
  top: 24px;
  transform: translateX(-50%);
  border-radius: 11px;
  overflow: hidden;
  width: 340px;
  display: flex;
}

.map__tab{
  width: 50%;
  height: 44px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 500;
  font-size: 16px;
  background-color: rgba(#EDEEF3, .85);
  color: #6E3F52;
  cursor: pointer;
  &.active{
    background-color: #6E3F52;
    color: #EDEEF3;
  }
}

.section-about {
  margin-top: -500px;
  background: linear-gradient(180deg, #6E3F52 19.27%, #463848 50%, #3F6E5B 83.85%, #463848 100%);
  padding-bottom: 500px;
}

.about__panel{
  margin-bottom: 24px;
  overflow: hidden;
  border-radius: 24px;
  position: relative;
  &:after{
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
  &--right{
    .about__tag{
      left: 0;
      top: 0;
    }
    .about__info{
      right: 24px;
      top: 24px;
    }
  }
  &--left{
    .about__tag{
      right: 0;
      top: 0;
    }
    .about__info{
      left: 24px;
      top: 24px;
    }
  }
}

.about__tag{
  position: absolute;
  height: 34px;
  width: 130px;
  background-color: #3F6E5B;
  border-radius: 11px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
  font-weight: 500;
  font-size: 14px;
  line-height: 100%;
  z-index: 20;
}

.about__info{
  max-width: 390px;
  position: absolute;
  z-index: 20;
  height: calc(100% - 48px);
  display: flex;
  flex-wrap: wrap;
  align-self: flex-start;
}


.about__text{
  width: 100%;
  font-weight: 500;
  font-size: 16px;
  line-height: 140%;
  z-index: 20;
}


.about__title{
  font-weight: 900;
  font-size: 56px;
  line-height: 100%;
  z-index: 20;
  color: #EDEEF3;
  display: flex;
  align-items: flex-end;
}


.about__description {
  padding-top: 196px;
  padding-bottom: 190px;
  display: flex;
  justify-content: center;
}


.section-company{
  padding-bottom: 80px;
}

.company__top {
  display: flex;
  justify-content: space-between;
  margin-bottom: 95px;
}

.company__title{
  font-weight: 500;
  font-size: 180px;
  line-height: 90%;
  margin-bottom: 62px;
}

.company__link{
  display: flex;
  justify-content: flex-end;
  overflow: hidden;
  &.--mobile{
    display: none;
  }
  a{
    position: relative;
    display: inline-block;
    font-weight: 500;
    font-size: 30px;
    line-height: 90%;
    color: #E8A9B8;
    &:after{
      content: "";
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 100%;
      margin-right: 28px;
      width: 397px;
      height: 12px;
      background: url(../img/company/arrow.svg) no-repeat right center / 397px auto;
    }
  }
}


.company__text {
  max-width: 500px;
  padding-left: 15px;
  p{
    font-weight: 500;
    font-size: 20px;
    line-height: 140%;
    margin-bottom: 30px;
  }
}

.company__bottom {
  display: flex;
  justify-content: space-between;
}

.company__item{
  &.--i1{
    padding-left: 65px;
    background: url(../img/company/i-1.svg) no-repeat 0 0 / 158px auto;
  }
  &.--i2{
    background: url(../img/company/i-2.svg) no-repeat 37px 0 / 144px auto;
  }
  &.--i3{
    background: url(../img/company/i-3.svg) no-repeat 60px 0 / 158px auto;
  }
}

.company__item-title{
  padding-top: 43px;
  font-weight: 500;
  font-size: 82px;
  line-height: 90%;
  margin-bottom: 9px;
}

.company__item-text{
  font-weight: 600;
  font-size: 20px;
  line-height: 140%;
}


.section-slider{
  padding-bottom: 80px;
}

.steps-slider{
  width: 100%;
}

.slide-video{
  min-width: 660px;
  width: 660px;
}

.steps-slide{
  padding-left: 24px;
  padding-right: 24px;
}

.slide-video,
.slide-photo{
  position: relative;
}

.slider__tag{
  right: 0;
  background-color: #6E3F52;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 12px;
  line-height: 100%;
  &:before{
    content: "";
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: #E8A9B8;
    margin-right: 4px;
  }
}

.slider__buttons {
  display: flex;
  position: absolute;
  left: 20px;
  bottom: 12px;
}

.slider__button {
  font-weight: 500;
  font-size: 14px;
  line-height: 90%;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba( #463848, .85);
  padding-left: 12px;
  padding-right: 12px;
  border-radius: 11px;
  cursor: pointer;
  &.active{
    background-color: #6E3F52;
    .svgsprite._video{
      fill: #E8A9B8;
    }
  }
  &:not(:last-child){
    margin-right: 8px;
  }
  .svgsprite._video{
    width: 14px;
    height: 10px;
    fill: #EDEEF3;
    margin-right: 8px;
  }
}


.slider__title{
  display: flex;
  justify-content: center;
  margin-top: 40px;

  &.--right{
    &:after{
      width: calc(50% + 24px);
      right: -24px;
      left: auto;
    }
  }

  span{
    padding-left: 24px;
    padding-right: 24px;
    background-color: #463848;
    display: inline-block;
    z-index: 10;
    position: relative;
  }

  &:after{
    content: "";
    position: absolute;
    width: calc(100% + 48px);
    left: -24px;
    height: 3px;
    background-color: rgba(#AAA3B4, .25);
    top: 50%;
    transform: translateY(-50%);
  }
}

.slider__resize{
  position: absolute;
  right: 16px;
  bottom: 12px;
  border-radius: 11px;
  background-color: rgba(#463848, .85);
  display: flex;
  align-items: center;
  z-index: 10;
  height: 35px;
  padding-left: 10px;
  padding-right: 10px;
  cursor: pointer;
}

.slide-photo{
  min-width: 410px;
  width: 410px;
  position: relative;
  
}

.slide-photo__wrap{
  position: relative;
  height: 530px;
  padding: 1px;
  border-radius: 24px;
  overflow: hidden;
  background: linear-gradient(180deg, rgba(70, 56, 72, 0) 50%, #6E3F52 100%);
  &:after{
    content: "";
    position: absolute;
    bottom: 1px;
    width: calc(100% - 2px);
    left: 1px;
    height: calc(100% - 2px);
    background: linear-gradient(180deg, rgba(70, 56, 72, 0) 50%, #463848 100%);
    border-radius: 24px;
  }
}

.slide-photo__pic{
  position: absolute;
  left: 1px;
  top: 1px;
  width: calc(100% - 2px); 
  height: calc(100% - 2px);
  border-radius: 24px;
}


.slide__date{
  position: absolute;
  top: 18px;
  left: 36px;
  font-weight: 600;
  font-size: 18px;
  line-height: 120%;
  background-color:  #3F6E5B;
  border-radius: 8px;
  padding-left: 12px;
  padding-right: 12px;
  height: 30px;
  display: flex;
  align-items: center;
  z-index: 20;
}

.slide__info{
  position: absolute;
  left: 0;
  bottom: 0;
  padding-left: 36px;
  padding-bottom: 24px;
  z-index: 20;
}

.slide__title{
  font-weight: 600;
  font-size: 24px;
  line-height: 120%;
  margin-bottom: 8px;
}

.slide__text{
  font-weight: 500;
  font-size: 14px;
  line-height: 120%;
  max-width: 360px;
}

.slide__pic{
  position: relative;
  width: 100%;
  height: 100%;
  height: 530px;
  border-radius: 24px;
  overflow: hidden;
}

.section-mainlink {
  padding-bottom: 160px;
}

.main-link{
  height: 300px;
  padding: 0 177px 0 160px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: url(../img/main-link-bg.png) no-repeat center / cover;
  border-radius: 24px;
}


.main-link__text{
  font-weight: 900;
  font-size: 56px;
  line-height: 100%;
}


.main-link__arrow {
  width: 100%;
  max-width: 400px;
}



.section-flats{
  margin-top: -500px;
  position: relative;
  img{
    width: 100%;
  }
  svg{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
  }
}

.elem{
  cursor: pointer;
  &:hover{
    
    path{
      opacity: 1;
    }
    & > path{
      opacity: .75;
    }
    circle{
      r: 26;
      fill: #6E3F52;
    }
  }
  path{
    opacity: 0;
  }
  circle {
    transition: .4s;
  }
  circle + path{
    opacity: 1;
  }
  
}


.flats-info{
  position: absolute;
  left: calc((100% - 1800px) / 2);
  top: 60px;
  max-width: 510px;
  width: 100%;
  z-index: 50;
}

@media(max-width: 1850px){
  .flats-info{
    left: 30px;
  }
}

.flats__btns {
  display: flex;
  justify-content: space-between;
  border-radius: 11px;
  overflow: hidden;
  max-width: 320px;
  margin-bottom: 36px;
}

.tabs__item{
  width: 50%;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-size: 14px;
  line-height: 100%;
  background-color: #DBDCE8;
  color: #3F6E5B;
  transition: .4s;
  cursor: pointer;
  &.active{
    background-color: #3F6E5B;
    color: #fff;
  }
}

.flat__title{
  font-weight: 900;
  font-size: 56px;
  line-height: 100%;
  margin-bottom: 24px;
}

.flat__text{
  font-weight: 500;
  font-size: 24px;
  line-height: 120%;
}

.navigator{
  display: flex;
  justify-content: space-between;
  background: #463848;
  border-radius: 24px;
  padding: 24px 24px 24px 48px;
  min-width: 857px;
  max-width: 857px;
}




.navigator__title{
  font-weight: 500;
  font-size: 30px;
  line-height: 110%;
  margin-bottom: 18px;
  max-width: 464px;
}

.navigator__input{
  position: relative;
  margin-bottom: 8px;
  
  input{
    width: 100%;
    height: 51px;
    background-color: transparent;
    border: none;
    font-weight: 600;
    font-size: 30px;
    line-height: 100%;
    &::placeholder{
      font-weight: normal;
    }
    &:focus + ::after{
      background-color: red;
    }
  }
}


.navigator__input-line{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background: #AAA3B4;
    border-radius: 8px;
    height: 3px;
    input:focus + &{
      background-color: #3F6E5B;
    }
}


.navigator__description{
  font-weight: 500;
  font-size: 14px;
  line-height: 84%;
}


.navigator__btn{
  width: 184px;
  height: 184px;
  border-radius: 20px;
  background: #6E3F52 url(../img/map/button.svg) no-repeat center / 117px auto;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 500;
  font-size: 20px;
  line-height: 120%;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}


.navigator__btn-time{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 20;
  background-color: #6E3F52;
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
  &.active{
    display: flex;
  }
}

.navigator__btn-time-minutes{
  font-weight: 500;
  font-size: 72px;
  line-height: 100%;
  transform: translateY(-10px);
}

.navigator__btn-time-text{
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 23px;
  font-weight: 500;
  font-size: 20px;
  line-height: 120%;
}

.map__delete-navigator{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 20;
  display: none;
  &.active{
    display: block;
  }
  span{
    display: flex;
    width: 50%;
    background-color:  #3F6E5B;
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    font-weight: 500;
    font-size: 16px;
    line-height: 100%;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
}


.navigator__point{
  min-width: 250px;
  max-width: 250px;
  height: 232px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid #463848;
  border-radius: 24px;
  overflow: hidden;
  position: relative;
}


.navigator__point-number{
  font-weight: 500;
  font-size: 100px;
  line-height: 100%;
  transform: translateY(-10px);
}

.navigator__point-text{
  font-weight: 500;
  font-size: 20px;
  line-height: 120%;
  position: absolute;
  left: 50%;
  bottom: 23px;
  transform: translateX(-50%);
  width: 100%;
  text-align: center;
}


.navigator__route{
  position: absolute;
  border-radius: 11px;
  right: 0;
  top: 0;
  padding-left: 14px;
  padding-right: 14px;
  background-color: #463848;
  height: 34px;
  display: flex;
  align-items: center;
  cursor: pointer;
  font-weight: 500;
  font-size: 14px;
  line-height: 100%;
}

.section-stepspage{
  .main-top-left{
    padding-left: 0px;
  }

  .main__right{
    width: 160px;
  }

  .slide-video{
    width: 100%;
    padding-left: 0px;
    padding-right: 0px;
  }
  
  .slide-photo{
    width: 100%;
  }
}

.timeline{
  position: relative;
  padding-top: 100px;
  padding-bottom: 135px;
  &:after{
    position: absolute;
    content: "";
    left: 50%;
    top: 0;
    width: 2px;
    height: 100%;
    background-color: rgba(#AAA3B4 , .25);
    z-index: 10;
  }
}
.timeline__item{
  position: relative;
  z-index: 20;
  &:not(:first-child){
    margin-top: -120px;
  }
  .slide-photo{
    width: calc(50% - 96px);
    min-width: auto;
    max-width: 100%;
    padding-left: 0px;
    padding-right: 0px;
  }
  .slide-photo__wrap{
    height: 480px;
  }
  .slider__title{
    position: absolute;
    left: 100%;
    top: 50%;
    transform: translateY(-50%);
    margin-top: 0px;
    margin-left: 49px;
    color: #AAA3B4;
    span{
      padding-left: 0px;
      padding-right: 0px;
      font-size: 36px;
      line-height: 90%;
      padding-top: 24px;
      padding-bottom: 24px;
    }
    &:after{
      display: none;
    }
  }
}



.timeline__item.--right{
  display: flex;
  justify-content: flex-end;
  .slider__title{
    left: auto;
    right: 100%;
    margin-right: 47px;
    margin-left: 0px;
  }
}


.year{
  font-weight: 500;
  font-size: 180px;
  line-height: 90%;
  text-align: center;
  color: #AAA3B4;
}


.docs{
  display: flex;
  flex-wrap: wrap;
  margin-right: -36px;
  margin-bottom: 140px;
}

.doc{
  border-radius: 11px;
  background-color: #EDEEF3;
  padding: 24px 30px;
  margin-right: 36px;
  margin-bottom: 36px;
  width: calc(20% - 36px);
}

.doc__date{
  font-weight: 500;
  font-size: 18px;
  line-height: 120%;
  margin-bottom: 8px;
  color: #3F6E5B;
}

.doc__name{
  font-weight: 500;
  font-size: 20px;
  line-height: 120%;
  color: #463848;
  max-width: 260px;
}

.doc__format{
  font-weight: 500;
  font-size: 18px;
  line-height: 120%;
  color: #3F6E5B;
}

.docs__title{
  font-weight: 500;
  font-size: 56px;
  line-height: 100%;
  margin-bottom: 48px;
}


.news{
  display: flex;
  justify-content: space-between;
  transition: .4s;
  transform: translateX(25%);
  &.active{
    transform: translateX(0); 
  }
}

.news__left{
  width: calc(50% - 11px);
}

.news__right{
  width: calc(50% - 11px);
  padding: 48px 72px;
  border-radius: 24px;
}

.news__item{
  border-top: 1px solid rgba(170, 163, 180, 0.25);
  padding: 48px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  position: relative;
  &:after{
    content: "";
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    height: calc(100% - 50px);
    width: 100%;
    background-color: #3F6E5B;
    border-radius: 11px;
    transition: .4s;
    opacity: 0;
  }
  &.active:after{
    opacity: 1;
