.form{
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 2000;
  background-color: #EDEEF3;
  display: none;
  .form__submit.--mobile{
    display: none;
  }
  
  
  .container{
    display: flex;
    justify-content: space-between;
    overflow-y: scroll;
    height: calc(100vh - 140px);

    padding-top: 100px;
  }
}

.form__submit{
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 144px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 56px;
  line-height: 120%;
  color: #3F6E5B;
  background-color: #DBDCE8;
  transition: .4s;
  z-index: 200;
  &:hover{
    background-color: #3F6E5B;
    color: #DBDCE8;
  }
}

.form__left {
  width: calc(50% - 60px);
  max-width: 480px;
  margin-bottom: 150px;
}

.form__right{
  width: 50%;
}

.form__title{
  font-weight: 600;
  font-size: 56px;
  line-height: 120%;
  margin-bottom: 36px;
  color: #463848;
}

.form__description{
  font-weight: 500;
  font-size: 14px;
  line-height: 84%;
  color: #AAA3B4;
}

.form__item{
  max-width: 560px;
  input{
    width: 100%;
    height: 56px;
    border-bottom: 2px solid #DBDCE8;
    margin-bottom: 8px;
    color: #463848;
    font-weight: 500;
    font-size: 30px;
    line-height: 100%;
    &:focus + .form__description{
      color: #3F6E5B;
    }
  }
  textarea{
    width: 100%;
    height: 100px;
    border-bottom: 2px solid #DBDCE8;
    margin-bottom: 8px;
    color: #463848;
    font-weight: 500;
    font-size: 30px;
    line-height: 100%;
    &:focus + .form__description{
      color: #3F6E5B;
    }
  }
  margin-bottom: 30px;
}



.form__undertitle{
  font-weight: 500;
  font-size: 18px;
  line-height: 160%;
  margin-bottom: 110px;
  color: #463848;
}

.form__close{
  position: absolute;
  top: 36px;
  right: 48px;
  z-index: 100;
}

.form__switch{
  width: 36px;
  height: 16px;
  border-radius: 40px;
  background-color: #DBDCE8;
  position: relative;
  transition: .4s;
  cursor: pointer;
  margin-right: 24px;

  &.active{
    background-color: #3F6E5B;
    &:after{
      left: 22px;
      background-color: #EDEEF3;
    }
  }
  &:after{
    content: "";
    position: absolute;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: #3F6E5B;
    top: 2px;
    left: 2px;
    transition: .4s;
  }
}

.form__switch-wrap {
  display: flex;
  font-weight: 500;
  font-size: 12px;
  line-height: 120%;
  color: #463848;
}

.gallery {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  display: none;
}

.gallery__slider{
  position: relative;
  &:after{
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 300px;
    background: linear-gradient(180deg, rgba(70, 56, 72, 0) 5.52%, #463848 100%);
    z-index: 10;
  }
  .swiper-slide{
    height: 100vh;
  }
}

.gallery__thumbs{
  position: absolute;
  bottom: 24px;
  left: 0;
  padding-left: 96px;
  z-index: 20;
  .swiper-slide{
    height: 120px;
    width: 180px;
    border-radius: 11px;
  overflow: hidden;
  }
}
.gallery__info {
  position: absolute;
  left: 96px;
  bottom: 200px;
  z-index: 100;
  .slide__title{
    display: flex;
    align-items: center;
  }
  .slide__date{
    position: static;
    margin-right: 8px;
  }
}
.galery__close{
  cursor: pointer;
  position: absolute;
  right: 24px;
  top: 24px;
  z-index: 50;
  cursor: pointer;
}

.form__succes{
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #fff;
  z-index: 300;
  display: none;
  &:after{
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color:  rgba(63, 110, 91, 0.85);
    transform: translateY(100%);
    z-index: 30;
    transition: 1s;
    transition-delay: 2s;
  }
  &.active{
    &:after{
      opacity: 1;
      transition-delay: 2s;
      transform: translateY(0);
    }
    .form__succes__bg{
      opacity: 1;
      transition-delay: 1s;
    }
    .form__succes__content{
      top: 50%;
      transition-delay: 3s;
    }

  }
}


.form__succes__bg{
  background: url(../img/flats/bg.png) no-repeat center / cover;
  opacity: 0;
  z-index: 20;
  transition: 1s;
  transition-delay: 1s;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.form__succes__content{
  position: absolute;
  left: 50%;
  top: 170%;
  transform: translate(-50%, -50%);
  z-index: 100;
  transition-delay: 3s;
  transition: 1s;
  img{
    display: block;
    margin: 0 auto 58px;
  }
}

.form__succes-title{
  text-align: center;
  font-weight: 500;
  font-size: 36px;
  line-height: 160%;
  color: #fff;
}

.form__succes-description{
  color: #fff;
  font-weight: 500;
  font-size: 24px;
  line-height: 160%;
  text-align: center;
}


