.header{
  display: flex;
  justify-content: space-between;
  padding-top: 32px;
  margin-bottom: 105px;
  position: absolute;
  width: 100%;
  z-index: 200;
}

.burger{
  width: 83px;
  height: 76px;
  border-radius: 12px;
  background-color: #6E3F52;
  box-shadow: 0px 24px 48px #6E3F52;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  transition: .4s;
  cursor: pointer;
  position: relative;
  &:hover{
    box-shadow:  0px 12px 28px #6E3F52;
  }
}

.burger__ico{
  margin-bottom: 8px;
}

.burger__text{
  font-size: 14px;
  line-height: 90%;
  font-weight: bold;
}

.header__right{
  display: flex;
  justify-content: flex-end;
}

.btn.header__buy{
  height: 43px;
  margin-left: 36px;
}

.header__left{
  width: calc(50% - 120px);
}

.header__right{
  align-items: center;
  width: calc(50% - 120px);
}

.header__phone{
  font-size: 24px;
  line-height: 90%;
}

.menu{
  position: absolute;
  left: 0;
  top: 32px;
  width: 100%;
  max-width: 487px;
  z-index: 200;
  background-color: #6E3F52;
  border-radius: 0px 24px 24px 0px;
  padding: 36px 0 80px 90px;
  display: none;
}

.menu__close{
  position: absolute;
  left: 36px;
  top: 36px;
  width: 18px;
  height: 18px;
  background: url(../img/close-grey.svg) no-repeat center / contain;
  cursor: pointer;
}

.menu__item{
  &:not(:last-child){
    margin-bottom: 48px;
  }
}

.menu__title{
  font-size: 18px;
  line-height: 120%;
  color: #AAA3B4;
  margin-bottom: 30px;
}

.menu__link{
  font-weight: 500;
  font-size: 24px;
  line-height: 120%;
  margin-bottom: 18px;
}