.btn{
  display: flex;
  align-items: center;
  padding-left: 24px;
  padding-right: 24px;
  border-radius: 11px;
  font-weight: 500;
  font-size: 18px;
  line-height: 140%;
  transition: .4s;
  span{
    position: relative;
    z-index: 10;
    transform: translateY(-1px);
  }
  &.--green{
    height: 37px;
    background: linear-gradient(#3F6E5B 0% , #325446);
    &:after{
      content: "";
      position: absolute;
      width: calc(100% - 2px);
      height: calc(100% - 2px);
      border-radius: 11px;
      left: 1px;
      top: 1px;
      background-color: #3F6E5B;
      transition: .4s;
    }
    &:hover{
      background: #DBDCE8;
      color: #AAA3B4;
      border-color: #DBDCE8;
      &:after{
        background-color: #DBDCE8;
      }
    }
  }
  &.--transparent{
    border: 1px solid rgba(219, 220, 232, 0.5); 
  }
}