.svgsprite {
    display:inline-block;
    fill:currentColor;
    vertical-align:middle;

    &._dropdown {
    	width:20px;
    	height:12px;
    }
    &._fb {
    	width:8px;
    	height:17px;
    }
    &._in {
    	width:16px;
    	height:16px;
    }
    &._video {
    	width:14px;
    	height:11px;
    }
    &._vk {
    	width:17px;
    	height:10px;
    }
}